<template>
  <el-row>
    <week-tab v-on:gettab="gettab($event)" :weeknum="weeks" />
    <transition name="el-fade-in">
      <el-col>
        <el-timeline>
          <transition-group name="el-zoom-in-top">
            <el-timeline-item
              v-for="(item, index) in showdata"
              :timestamp="item.R更新时间str"
              placement="top"
              draggable="false"
              :key="index"
              type="primary"
              :title="item.R动画名称"
            >
              <a :href="mian_url + '/detail/' + item.AID" target="_blank">
                <el-card
                  draggable="false"
                  style="border: none"
                  :body-style="{
                    padding: '0px',
                    background: type_color(item.AID),
                  }"
                  v-if="reload"
                  :style="{ background: type_color(item.AID) }"
                >
                  <el-col
                    :xs="8"
                    :sm="6"
                    :md="4"
                    :lg="3"
                    :xl="3"
                    draggable="true"
                    @mousedown.native="move"
                    @dragend.native="up(item.AID, item.R动画名称, item)"
                  >
                    <el-card
                      draggable="true"
                      style="border: none"
                      :body-style="{
                        padding: '0px',
                      }"
                    >
                      <div style="position: relative">
                        <img
                          draggable="false"
                          :src="item.R封面图小"
                          class="image"
                          width="100%"
                          style="border-radius: 3px 3px 0px 0px"
                        />
                        <transition v-if="reload" name="el-fade-in-linear">
                          <img
                            src="..\public\heart.svg"
                            v-if="ishow(item.AID)"
                            id="love"
                          />
                          <div v-if="isBlack(item.id)" id="black">
                            黑名单
                          </div></transition
                        >
                        <span id="NewTitle">{{ item.R新番标题 }}</span>
                      </div>
                      <div id="Title">
                        <span>{{ item.R动画名称 }}</span>
                      </div>
                    </el-card>
                  </el-col>
                  <h4 id="Title">{{ item.R动画名称 }}</h4>
                  <p id="Rjiessao">{{ item.R简介 }}</p>
                </el-card>
              </a>
            </el-timeline-item>
          </transition-group>
        </el-timeline>
      </el-col>
    </transition>
  </el-row>
</template>

<script>
import WeekTab from "./WeekTab.vue";
import axios from "axios";
import DefaultData from "./DefaultData.vue";
import Info_box from "./info.vue";

export default {
  components: { WeekTab },
  name: "EveryWeek",

  data() {
    return {
      mian_url: DefaultData.Main_url,
      acg_colos: DefaultData.acg_colos,
      save: [],
      saveimg: [],
      weeks: this.getweekday(),
      reload: true,
      title: "",
      AID: 0,
      showdata: [],
      josn_data: [],
      MAXnum: 20,
      nownum: 0,
      contnum: 0,
      NowRuning: true,
    };
  },

  mounted() {
    var _this = this;
    this.nownum = 0;
    this.MAXnum = 5;

    axios
      .get("https://api.agefans.app/v2/home-list?update=1&recommend=1")
      .then((response) => {
        _this.save = response.data.XinfansInfo;
        this.$nextTick(() => {
          this.NowRuning = false;
          _this.loding();
        });
      })
      .catch((error) => {
        console.log(error);
        this.errored = true;
      })
      .finally(() => (this.loading = false));

    setTimeout(function () {
      if (_this.NowRuning) _this.autoadd();
    }, 500);

    window.onresize = () => {
      if (this.NowRuning) this.autoadd();
    };

    document
      .querySelector("#app > div > section > section > main ")
      .addEventListener("scroll", function () {
        if (
          document.querySelector("#app > div > section > section > main")
            .scrollTop +
            window.innerHeight >=
          document.querySelector("#app > div > section > section > main > div ")
            .offsetHeight
        ) {
          _this.regetdata();
        }
      });
  },
  methods: {
    autoadd(num) {
      if (num == null) num = 0;
      if (
        document.querySelector("#app > div > section > section > main > div ")
          .offsetHeight <
        document.querySelector("#app > div > section > section > main")
          .offsetHeight +
          num
      ) {
        this.regetdata();
      }
    },
    getweekday() {
      var day = new Date().getDay();
      return String(day);
    },
    move(e) {
      let odiv = e.target; //获取目标元素
      //算出鼠标相对元素的位置
      let disX = e.clientX - odiv.offsetLeft;
      let disY = e.clientY - odiv.offsetTop;
      document.ondragenter = (e) => {
        //鼠标按下并移动的事件
        //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
        let left = e.clientX - disX;
        let top = e.clientY - disY;

        document.ondragend = () => {
          document.ondragenter = null;
          document.ondragend = null;
          let type = -1;
          if (left > 80 && top < -80) {
            type = 6;
            //正在看
          } else if (left < -80 && top > 80) {
            //黑名单
            type = 5;
          } else if (left > 80 && top > 80) {
            type = 4;
            //不想看
          } else if (left > 100 && left > top) {
            type = 0;
            //喜欢
          } else if (left < -100 && left < top) {
            type = 1;
            //准备看
          } else if (top < -100 && top < left) {
            type = 2;
            //没看完
          } else if (top > 100 && top > left) {
            type = 3;
            //已看完
          }
          if (type != -1) {
            this.reload_();
            this.$notify.info({
              title: "「" + this.title + "」",
              message: (
                <Info_box
                  info_num={type}
                  info_AID={this.AID}
                  info_json={this.josn_data}
                />
              ),
              showClose: false,
              duration: 1000,
            });
            this.runstart(this.showdata, 0);
            if (this.NowRuning) this.autoadd(300);
          }
        };
      };
    },
    up(AID, title, josn) {
      var rejson = {
        AID: AID,
        Title: title,
        NewTitle: josn.R新番标题,
        PicSmall: josn.R封面图小,
        Href: "/detail/" + AID,
      };

      this.josn_data = rejson;
      this.AID = AID;
      this.title = title;
    },
    reload_() {
      this.reload = false;
      this.$nextTick(() => {
        this.reload = true;
      });
    },
    runstart(old_data, type) {
      var data = old_data;
      if (type == 0) this.showdata = [];
      if (data != null)
        for (let index = 0; index < data.length; index++) {
          if (this.reshow(data[index].AID)) {
            this.showdata = this.showdata.concat(data[index]);
          }
        }
    },
    reshow(id) {
      var type_sw = JSON.parse(window.localStorage.getItem("type_sw"));
      if (type_sw != null) {
        var data = JSON.parse(window.localStorage.getItem(id));
        if (data != null) {
          for (let index = 0; index < type_sw.length; index++) {
            if (type_sw[index]) {
              if (data.type == index) {
                return false;
              }
            }
          }
          return true;
        }
        return true;
      }
      return true;
    },
    type_color(id) {
      var data = JSON.parse(window.localStorage.getItem(id));
      if (data != null)
        if (data.type == -1 || data.type == 0 || data.type == 5) {
          return;
        } else {
          return this.acg_colos[data.type];
        }
    },
    isBlack(id) {
      var data = JSON.parse(window.localStorage.getItem(id));
      if (data != null)
        if (data.love == -1) {
          return true;
        }
    },
    ishow(id) {
      var data = JSON.parse(window.localStorage.getItem(id));
      if (data != null)
        if (data.love == 1) {
          return true;
        }
    },
    gettab(num) {
      this.showdata = [];
      this.saveimg = [];
      this.nownum = 0;
      this.MAXnum = 5;
      this.weeks = String(num);
      this.loding();
    },
    loding() {
      for (let index = 0; index < this.save.length; index++) {
        if (this.save[index].wd == this.weeks) {
          this.contnum++;
          this.saveimg = this.saveimg.concat(this.save[index]);
        }
      }
      this.regetdata();

      this.NowRuning = true;
    },
    regetdata() {
      for (let index = this.nownum; index < this.saveimg.length; index++) {
        this.getimg(this.saveimg[index].id);
        this.nownum++;
        if (this.nownum >= this.MAXnum) {
          this.MAXnum += 5;
          return;
        }
      }
    },
    getimg(index) {
      var _this = this;
      axios
        .get("https://api.agefans.app/v2/detail/" + index)
        .then((response) => {
          _this.showdata = _this.showdata.concat(response.data.AniInfo);
          this.runstart(this.showdata, 0);
        })
        .catch((error) => {
          console.log(error);
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    },
  },
};
</script>

<style lang="scss" scoped>
a {
  text-decoration: none;
}
.el-card {
  margin: 5px;
}
#black {
  position: absolute;
  left: 0px;
  top: 40%;
  white-space: nowrap;
  width: 100%;
  height: 30px;
  background: #00024d;
  color: azure;
  font-size: 20px;
}
#love {
  position: absolute;
  left: 5px;
  top: 5px;
  white-space: nowrap;
}
#Title {
  padding: 14px;
  height: 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 100%;
}
#NewTitle {
  position: absolute;
  right: 2px;
  bottom: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 5px;
  background-color: #f4f0f0ce;
  overflow: hidden;
  border-radius: 2.5px;
  max-width: 80%;
  font-size: 10px;
  line-height: 10px;
}
#Rjiessao {
  text-align: start;
  padding: 20px;
}
</style>