<template>
  <el-row draggable="false">
    <el-col>
      <Check_teyp v-on:get_type="get_type($event)" />
    </el-col>
    <transition-group name="el-zoom-in-center">
      <el-col
        :xs="8"
        :sm="6"
        :md="4"
        :lg="3"
        :xl="3"
        v-for="item in showdata"
        :key="item.AID"
        draggable="true"
        @mousedown.native="move"
        @dragend.native="up(item.AID, item.Title, item)"
        :title="item.Title"
      >
        <a :href="mian_url + item.Href" target="_blank">
          <el-card
            style="border: none"
            :body-style="{ padding: '0px', background: type_color(item.AID) }"
          >
            <div style="position: relative">
              <img
                draggable="false"
                :src="(item.PicSmall)"
                class="image"
                width="100%"
                style="border-radius: 3px 3px 0px 0px"
              />
              <transition v-if="reload" name="el-fade-in-linear">
                <img
                  src="..\public\heart.svg"
                  v-if="ishow(item.AID)"
                  id="love"
                />
                <div v-if="isBlack(item.AID)" id="black">
                  黑名单
                </div></transition
              >
              <span id="NewTitle">{{ item.NewTitle }}</span>
            </div>
            <div id="Title">
              <span>{{ item.Title }}</span>
            </div>
          </el-card>
        </a>
      </el-col>
    </transition-group>
  </el-row>
</template>

<script>
import Info_box from "./info.vue";
import Check_teyp from "./CheckTeyp.vue";
import DefaultData from "./DefaultData.vue";

export default {
  components: { Check_teyp },
  name: "Love_",
  data() {
    return {
      mian_url: DefaultData.Main_url,
      acg_colos: DefaultData.acg_colos,
      See_type: 0,
      josn_data: {},
      reload: true,
      demo: false,
      title: "",
      AID: 0,
      showdata: [],
      sw: true,
      MAXnum: 20,
      itemnum: 1,
      Allnum: 0,
      nownum: 1,
    };
  },
  methods: {
    sendcontnum() {
      this.$emit("getcontnum", this.nownum - 1);
    },
    type_color(id) {
      var data = JSON.parse(window.localStorage.getItem(id));
      if (data != null)
        if (data.type == -1 || data.type == 0 || data.type == 5) {
          return;
        } else {
          return this.acg_colos[data.type];
        }
    },
    reload_() {
      this.reload = false;
      this.$nextTick(() => {
        this.reload = true;
      });
    },
    isBlack(id) {
      var data = JSON.parse(window.localStorage.getItem(id));
      if (data != null)
        if (data.love == -1) {
          return true;
        }
    },
    ishow(id) {
      var data = JSON.parse(window.localStorage.getItem(id));
      if (data != null)
        if (data.love == 1) {
          return true;
        }
    },
    up(AID, title, json) {
      this.AID = AID;
      this.title = title;
      this.josn_data = json;
    },
    redata() {
      let max_length = window.localStorage.length;
      for (let index = this.Allnum; index < max_length; index++) {
        this.Allnum++;
        let key = window.localStorage.key(index);
        if (key == "type_sw" || key == "bg") continue;
        let data = JSON.parse(window.localStorage.getItem(key));
        if (data.Josn_Data != null) {
          var re = false;
          if (data.type != -1 || data.love != 0) {
            if (this.See_type == data.type) {
              this.showdata = this.showdata.concat(data.Josn_Data);
              re = true;
            } else {
              if (this.See_type == 0)
                if (data.love == 1) {
                  this.showdata = this.showdata.concat(data.Josn_Data);
                  re = true;
                }
              if (this.See_type == 5)
                if (data.love == -1) {
                  this.showdata = this.showdata.concat(data.Josn_Data);
                  re = true;
                }
            }

            if (re) {
              this.itemnum++;
              this.nownum++;
              if (this.itemnum >= this.MAXnum) {
                this.itemnum == 0;
                return;
              }
            }
          }
        }
      }
    },
    move(e) {
      let odiv = e.target; //获取目标元素
      //算出鼠标相对元素的位置
      let disX = e.clientX - odiv.offsetLeft;
      let disY = e.clientY - odiv.offsetTop;
      document.ondragenter = (e) => {
        //鼠标按下并移动的事件
        //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
        let left = e.clientX - disX;
        let top = e.clientY - disY;

        document.ondragend = () => {
          document.ondragenter = null;
          document.ondragend = null;
          let type = -1;
          if (left > 80 && top < -80) {
            type = 6;
            //正在看
          } else if (left < -80 && top > 80) {
            //黑名单
            type = 5;
          } else if (left > 80 && top > 80) {
            type = 4;
            //不想看
          } else if (left > 100 && left > top) {
            type = 0;
            //喜欢
          } else if (left < -100 && left < top) {
            type = 1;
            //准备看
          } else if (top < -100 && top < left) {
            type = 2;
            //没看完
          } else if (top > 100 && top > left) {
            type = 3;
            //已看完
          }
          if (type != -1) {
            this.reload_();
            this.$notify.info({
              title: "「" + this.title + "」",
              message: (
                <Info_box
                  info_num={type}
                  info_AID={this.AID}
                  info_json={this.josn_data}
                />
              ),
              showClose: false,
              duration: 1000,
            });
            this.showdata = [];
            this.Allnum = 0;
            this.itemnum = 1;
            this.MAXnum = this.nownum;
            this.redata();
            this.autoadd();
            this.sendcontnum();
          }
        };
      };
    },
    get_type(radio) {
      this.showdata = [];
      this.nownum = 1;
      this.itemnum = 1;
      this.MAXnum = 20;
      this.Allnum = 0;
      this.See_type = radio;

      this.$nextTick(() => {
        this.redata();
        this.sendcontnum();
      });
      var _this = this;
      setTimeout(function () {
        _this.autoadd();
      }, 500);
    },
    autoadd() {
      if (
        document.querySelector("#app > div > section > section > main >div")
          .offsetHeight <
        document.querySelector("#app > div > section > section > main ")
          .offsetHeight
      ) {
        this.itemnum = 1;
        this.redata();
        this.sendcontnum();
      }
    },
    reurl(str) {
      var data = str.split("/").slice(2).join("/");
      return "https://" + data;
    },
  },
  mounted() {
    var _this = this;

    this.showdata = [];
    this.redata();
    this.sendcontnum();

    setTimeout(function () {
      _this.autoadd();
    }, 500);

    window.onresize = () => {
      this.autoadd();
    };

    document
      .querySelector("#app > div > section > section > main ")
      .addEventListener("scroll", function () {
        if (
          document.querySelector("#app > div > section > section > main")
            .scrollTop +
            window.innerHeight >=
          document.querySelector("#app > div > section > section > main >div")
            .offsetHeight
        ) {
          _this.itemnum = 1;
          _this.redata();
          _this.sendcontnum();
        }
      });
  },
};
</script>

<style>
a {
  text-decoration: none;
}
.el-card {
  margin: 5px;
}
#black {
  position: absolute;
  left: 0px;
  top: 40%;
  white-space: nowrap;
  width: 100%;
  height: 30px;
  background: #00024d;
  color: azure;
  font-size: 20px;
}
#love {
  position: absolute;
  left: 5px;
  top: 5px;
  white-space: nowrap;
}
#Title {
  padding: 14px;
  height: 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 100%;
}
#NewTitle {
  position: absolute;
  right: 2px;
  bottom: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 5px;
  background-color: #f4f0f0ce;
  overflow: hidden;
  border-radius: 2.5px;
  max-width: 80%;
  font-size: 10px;
  line-height: 10px;
}
</style>