<template>
  <el-row draggable="false"  style="background: #ffffff00;">
    <transition-group name="el-zoom-in-center">
      <el-col
        :xs="8"
        :sm="6"
        :md="4"
        :lg="3"
        :xl="3"
        v-for="item in showdata"
        :key="item.AID"
        draggable="true"
        @mousedown.native="move"
        @dragend.native="up(item.AID, item.Title, item)"
        :title="item.Title"
      >
        <a :href="mian_url + item.Href" target="_blank">
          <el-card
            style="border: none"
            :body-style="{ padding: '0px', background: type_color(item.AID) }"
          >
            <div style="position: relative">
              <img
                draggable="false"
                v-bind:src="(item.PicSmall)"
                class="image"
                width="100%"
                style="border-radius: 3px 3px 0px 0px"
              />
              <transition v-if="reload" name="el-fade-in-linear">
                <img
                  src="..\public\heart.svg"
                  v-if="ishow(item.AID)"
                  id="love"
                />
                <div v-if="isBlack(item.AID)" id="black">
                  黑名单
                </div></transition
              >
              <span id="NewTitle">{{ item.NewTitle }}</span>
            </div>
            <div id="Title">
              <span>{{ item.Title }}</span>
            </div>
          </el-card>
        </a>
      </el-col>
    </transition-group>
  </el-row>
</template>

<script>
import axios from "axios";
import Info_box from "./info.vue";
import DefaultData from "./DefaultData.vue";

export default {
  name: "Data_",
  Info_box,
  props: {
    Data_type: String,
  },
  data() {
    return {
      josn_data: JSON,
      mian_url: DefaultData.Main_url,
      acg_colos: DefaultData.acg_colos,
      reload: true,
      demo: false,
      title: "",
      AID: 0,
      showdata: [],
      sw: true,
      MAXnum: 20,
      page: 1,
    };
  },
  methods: {
    reshow(id) {
      var type_sw = JSON.parse(window.localStorage.getItem("type_sw"));
      if (type_sw != null) {
        var data = JSON.parse(window.localStorage.getItem(id));
        if (data != null) {
          for (let index = 0; index < type_sw.length; index++) {
            if (type_sw[index]) {
              if (data.type == index) {
                return false;
              }
            }
          }
          return true;
        }
        return true;
      }
      return true;
    },
    type_color(id) {
      var data = JSON.parse(window.localStorage.getItem(id));
      if (data != null)
        if (data.type == -1 || data.type == 0 || data.type == 5) {
          return;
        } else {
          return this.acg_colos[data.type];
        }
    },
    reload_() {
      this.reload = false;
      this.$nextTick(() => {
        this.reload = true;
      });
    },
    isBlack(id) {
      var data = JSON.parse(window.localStorage.getItem(id));
      if (data != null)
        if (data.love == -1) {
          return true;
        }
    },
    ishow(id) {
      var data = JSON.parse(window.localStorage.getItem(id));
      if (data != null)
        if (data.love == 1) {
          return true;
        }
    },
    up(AID, title, josn) {
      this.josn_data = josn;
      this.AID = AID;
      this.title = title;
    },
    move(e) {
      let odiv = e.target; //获取目标元素
      //算出鼠标相对元素的位置
      let disX = e.clientX - odiv.offsetLeft;
      let disY = e.clientY - odiv.offsetTop;
      document.ondragenter = (e) => {
        //鼠标按下并移动的事件
        //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
        let left = e.clientX - disX;
        let top = e.clientY - disY;

        document.ondragend = () => {
          document.ondragenter = null;
          document.ondragend = null;
          let type = -1;
          if (left > 80 && top < -80) {
            type = 6;
          } else if (left < -80 && top > 80) {
            //黑名单
            type = 5;
          } else if (left > 80 && top > 80) {
            type = 4;
            //不想看
          } else if (left > 100 && left > top) {
            type = 0;
            //喜欢
          } else if (left < -100 && left < top) {
            type = 1;
            //准备看
          } else if (top < -100 && top < left) {
            type = 2;
            //没看完
          } else if (top > 100 && top > left) {
            type = 3;
            //已看完
          }

          if (type != -1) {
            this.$notify.info({
              title: "「" + this.title + "」",
              message: (
                <Info_box
                  info_num={type}
                  info_AID={this.AID}
                  info_json={this.josn_data}
                />
              ),
              showClose: false,
              duration: 1000,
            });
          }
          this.reload_();
          this.runstart(this.showdata, 0);
        };
      };
    },
    runstart(old_data, type) {
      var data = old_data;
      if (type == 0) this.showdata = [];
      if (data != null)
        for (let index = 0; index < data.length; index++) {
          if (this.reshow(data[index].AID)) {
            this.showdata = this.showdata.concat(data[index]);
          }
        }
    },
    autoadd() {
      if (
        document.querySelector("#app > div > section > section > main >div")
          .offsetHeight <
          document.querySelector("#app > div > section > section > main ")
            .offsetHeight &&
        this.page * 20 < this.MAXnum
      ) {
        var _this = this;
        this.page++;
        axios
          .get(
            "https://api.agefans.app/v2/" +
              _this.Data_type +
              "?page=" +
              _this.page +
              "&size=20"
          )
          .then((response) => {
            _this.MAXnum = response.data.AllCnt;
            _this.runstart(response.data.AniPre, 1);
            _this.sw = true;
          })
          .catch((error) => {
            console.log(error);
            _this.errored = true;
          })
          .finally(() => (_this.loading = false));
      }
    },
    setatag() {
      this.$nextTick(() => {
        const links = document.querySelectorAll("a[href]");
        links.forEach((link) => {
          link.addEventListener("click", () => {
            // this.$electron.shell.openExternal(links.getAttribute("href"));
            link.src = link.getAttribute("href");
          });
        });
      });
    },
    reurl(str) {
      var data = str.split("/").slice(2).join("/");
      return "https://" + data;
    },
  },
  mounted() {
    var _this = this;
    axios
      .get("https://api.agefans.app/v2/" + this.Data_type + "?page=1&size=20")
      .then((response) => {
        this.MAXnum = response.data.AllCnt;
        this.runstart(response.data.AniPre, 1);
        setTimeout(function () {
          _this.autoadd();
          _this.setatag();
        }, 500);
      })
      .catch((error) => {
        console.log(error);
        this.errored = true;
      })
      .finally(() => (this.loading = false));

    window.onresize = () => {
      this.autoadd();
    };

    document
      .querySelector("#app > div > section > section > main ")
      .addEventListener("scroll", function () {
        if (
          document.querySelector("#app > div > section > section > main")
            .scrollTop +
            window.innerHeight >=
            document.querySelector("#app > div > section > section > main >div")
              .offsetHeight &&
          _this.page * 20 < _this.MAXnum
        ) {
          // 如果开关打开则加载数据
          if (_this.sw == true) {
            // 将开关关闭

            _this.page++;
            // console.log("ok--" + _this.page);
            axios
              .get(
                "https://api.agefans.app/v2/" +
                  _this.Data_type +
                  "?page=" +
                  _this.page +
                  "&size=20"
              )
              .then((response) => {
                // _this.showdata = _this.showdata.concat(response.data.AniPre);
                _this.MAXnum = response.data.AllCnt;
                _this.runstart(response.data.AniPre, 1);
                _this.sw = true;
              })
              .catch((error) => {
                console.log(error);
                _this.errored = true;
              })
              .finally(() => (_this.loading = false));

            _this.sw = false;
          }
        }
      });
  },
};
</script>

<style>
a {
  text-decoration: none;
}
.el-card__body {
  padding: 0;
}
.el-card {
  margin: 5px;
  border: none;
}
#black {
  position: absolute;
  left: 0px;
  top: 40%;
  white-space: nowrap;
  width: 100%;
  height: 30px;
  background: #00024d;
  color: azure;
  font-size: 20px;
}
#love {
  position: absolute;
  left: 5px;
  top: 5px;
  white-space: nowrap;
}
#Title {
  padding: 14px;
  height: 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 100%;
}
#NewTitle {
  overflow: hidden;
  position: absolute;
  right: 2px;
  bottom: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 5px;
  background-color: #f4f0f0ce;
  overflow: hidden;
  border-radius: 2.5px;
  max-width: 80%;
  font-size: 10px;
  line-height: 10px;
}

</style>